import useInputValidator from "~/compositions/useInputValidator";
import ErrorDisplay from "./ErrorDisplay";

export default {
  name: "FormInput",
  components: {
    ErrorDisplay,
  },
  props: {
    type: String,
    name: String,
    label: String,
    validators: Array,
  },
  setup(props, {
    emit
  }) {
    const {
      input,
      errors
    } = useInputValidator(
      props.value,
      props.validators || [],
      (value) => emit("input", value)
    );
    return {
      input,
      errors,
    };
  },
};
